import { Component }  from '@angular/core';

@Component({
    templateUrl: './word.component.html',
    selector: 'app-word',
    styleUrls: ['./word.component.css']
})

export class WordComponent {
    en: string = 'Hello';
    vn: string = 'Xin Chao';
    urlImage = 'https://cdn.auth0.com/blog/angular5/logo.png';
    forgot = false;

    toggleForgot() {
        this.forgot = !this.forgot;
    }
}